import React from "react"
import PropTypes from "prop-types"
import { Info } from "@material-ui/icons"

export default class ApiKeyAuth extends React.Component {
    constructor(props, context) {
        super(props, context)
    
        this.state = {
            showInfo: false,
        }

        this.infoToggle = this.infoToggle.bind(this)
    }

    infoToggle() {
        this.setState((state) => ({
            showInfo: !state.showInfo
        }))
    }

    render() {
        let { showInfo } = this.state

        return (
            <div className={"no-auth auth-simple"}>
                <h1>Authorization not available</h1>
                <div className="info-wrapper" onMouseEnter={this.infoToggle} onMouseLeave={this.infoToggle}>
                    <Info />
                    {showInfo && 
                        <div className="info-inner">
                            <div className="header">
                                <h1>No Authorization</h1>
                            </div>
                            <div className="body">
                                <div className="body-inner">
                                    <h1>Authorization isn't specified.</h1>
                                    <h1>Therefor the Try-Out functionality is not available.</h1>
                                </div>
                            </div>
                        </div>
                    }
                </div>
            </div>
        )
    }
}
