import React from "react"
import PropTypes from "prop-types"


export default class NavSubLink extends React.Component {
    static propTypes = {
        specSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,

        op: PropTypes.object.isRequired,
        position: PropTypes.number.isRequired,
        tag: PropTypes.string.isRequired,
    }

    constructor(props) {
        super(props)
        
        // window currently at position of this method
        this.state = {
            selected: false
        }

        let { op, processedTag, i } = props

        let operationId = op.getIn(["operation", "__originalOperationId"]) || op.getIn(["operation", "operationId"]) || op.get("id")
        let isShownKey = ["operations", processedTag, operationId, i]
        this.shownKey = isShownKey.join("-")

        this.listenToScroll = this.listenToScroll.bind(this)
        this.scrollTo = this.scrollTo.bind(this)
        this.navScroll = this.navScroll.bind(this)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.listenToScroll)
        this.interval = setInterval(() => {
            const opObject = document.getElementById(this.shownKey)
            opObject.addEventListener('click', () => this.navScroll())
        }, 3000)
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.listenToScroll)
        const opObject = document.getElementById(this.shownKey)
        opObject.removeEventListener('click', () => this.navScroll())
        clearInterval(this.interval)
    }

    navScroll() {
        let { processedTag } = this.props
        const mainNav = document.getElementById("navigation-wrapper")
        const thisTagNav = document.getElementById("navigation-link-" + processedTag)
        mainNav.scrollTo({
            top: thisTagNav.offsetTop, 
            left: 0, 
            behavior: 'smooth'
        })
    }

    listenToScroll() {
        let { position } = this.props
        this.setState({
            selected: position === document.getElementById(this.shownKey).offsetTop
        })
    }

    scrollTo() {
        window.scrollTo({ 
            top: document.getElementById(this.shownKey).offsetTop - 45, 
            left: 0, 
            behavior: 'smooth'
        })
    }

    render() {
        let { op } = this.props

        let { deprecated } = op.get("operation").toJS()
        let method = op.get("method")
        var title = op.getIn(["operation", "title"]) || op.getIn(["operation", "summary"]) || ""
        
        let { selected } = this.state
        
        if (title.length <= 1) {
            title = op.get("path")
        }

        return <div className={"navigation-link method" + (selected ? " selected" : "")} onClick={this.scrollTo} id={"navigation-" + this.shownKey}>
            <span className="navigation-linktag--sub">{ title }</span>
            {deprecated && <span className="navigation-linktag--sub deprecated">deprecated</span>}
            <div className={`method-icon ${deprecated ? "deprecated" : method} ${selected ? "selected" : ""}`}>
                <span className="navigation-linktag--sub chip">{ method.toUpperCase() }</span>
            </div>
        </div>
    }
}