import React from "react"
import PropTypes from "prop-types"
import { Info } from "@material-ui/icons"

export default class BaseURL extends React.Component {
    static propTypes = {
        specActions: PropTypes.object.isRequired,
        specSelectors: PropTypes.object.isRequired,
        authActions: PropTypes.object.isRequired,
        authSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired
    }


    constructor(props) {
        super(props)

        let url = props.authSelectors.systemUrl() || ""
        this.state = {
            showInfo: false,
            url: url,
            error: false
        }
        props.authActions.setSystemUrl(url)

        this.infoToggle = this.infoToggle.bind(this)
        this.onChange = this.onChange.bind(this)
        this.keyPress = this.keyPress.bind(this)
        this.confirm = this.confirm.bind(this)
    }

    componentDidMount() {
        const authButton = document.getElementById("auth-button")
        authButton.addEventListener('click', () => this.confirm())
    }

    componentWillUnmount() {
        const authButton = document.getElementById("auth-button")
        authButton.removeEventListener('click', () => this.confirm())
    }

    infoToggle() {
        this.setState((state) => ({
            showInfo: !state.showInfo
        }))
    }

    onChange = (e) => {
        let value = e.target.value
        this.setState({ url: value })
    }

    keyPress = (e) => {
        if (e.keyCode == 13) {
            this.confirm()
        }
    }

    confirm() {
        let { authActions } = this.props
        let { url } = this.state

        let valid = this.checkUrl(url)

        if (valid) {
            var length = url.length
            var lastChar = url.charAt(length - 1)

            if (lastChar == "/") {
                url = url.slice(0, length - 1)
            }
            
            authActions.setSystemUrl(url)
            document.activeElement.blur()
        } else {
            this.setState({ error: true })
        }
    }

    checkUrl(url) {
        var pattern = new RegExp('(https:\\/\\/)' + // protocol
            '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.?)+[a-z]{2,}|' + // domain name
            '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
            '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
            '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
            '(\\#[-a-z\\d_]*)?$', 'i'); // fragment locator
        return pattern.test(url);
    }

    render() {
        let { getComponent } = this.props

        const Input = getComponent("Input")

        let {
            showInfo,
            url,
            error
        } = this.state

        return (
            <div className="auth-simple" id="system-url" >
                <Input 
                    className={error ? "auth-input error" : "auth-input"} 
                    type="text" onChange={this.onChange} 
                    value={url} 
                    onKeyDown={this.keyPress} 
                    onAnimationEnd={() => this.setState({ error: false })} 
                    placeholder="System URL"
                />
                <div className="info-wrapper" onMouseEnter={this.infoToggle} onMouseLeave={this.infoToggle}>
                    <Info />
                    {showInfo &&
                        <div className="info-inner">
                            <div className="header">
                                <h1>System URL</h1>
                            </div>
                            <div className="body">
                                <div className="body-inner">
                                    <h1>used for try out</h1>
                                </div>
                                <div className="body-inner">
                                    <h1>- scheme (https only)</h1>
                                    <h1>- hostname (FGN, e.g. "is-dev2.brandmaker.com)</h1>
                                </div>
                            </div>
                        </div>
                    }
                </div>
            </div>
        )
    }

}

BaseURL.propTypes = {
    title: PropTypes.any,
    description: PropTypes.any,
    version: PropTypes.any,
    url: PropTypes.string
}
