import React, { Component } from "react"
import PropTypes from "prop-types"


import SyntaxHighlighter from 'react-syntax-highlighter'
import { CropFree, FilterNone } from "@material-ui/icons"


export default class HighlightCode extends Component {
    static propTypes = {
        value: PropTypes.string.isRequired,
        className: PropTypes.string,
    }

    constructor(props) {
        super(props)
        this.state = {
            showModal: false,
            copyHover: false,
            copied: false,
            bigHover: false
        }
        this.showModal = this.showModal.bind(this)
        this.hideModal = this.hideModal.bind(this)

        this.bigHoverOn = this.bigHoverOn.bind(this)
        this.bigHoverOff = this.bigHoverOff.bind(this)

        this.copyToClipboard = this.copyToClipboard.bind(this)
        this.copyHoverOn = this.copyHoverOn.bind(this)
        this.copyHoverOff = this.copyHoverOff.bind(this)
    }

    showModal() {
        this.setState({
            showModal: true,
            copyHover: false,
            bigHover: false,
            copied: false
        })
    }

    hideModal() {
        this.setState({
            showModal: false
        })
    }

    copyToClipboard() {
        navigator.clipboard.writeText(this.props.value)
        this.setState({ copied: true })
    }

    copyHoverOn() {
        this.setState({
            copyHover: true
        })
    }

    copyHoverOff() {
        this.setState({
            copyHover: false,
            copied: false
        })
    }

    bigHoverOn() {
        this.setState({
            bigHover: true,
            copyHover: false
        })
    }

    bigHoverOff() {
        this.setState({
            bigHover: false,
            copyHover: true
        })
    }

    render() {
        //return <div />
        let { value, className, getComponent, max, showLineNumbers, wrapLines } = this.props
        className = className || ""

        let {
            copyHover,
            copied,
            showModal,
            bigHover
        } = this.state

        showLineNumbers = showLineNumbers || false
        wrapLines = true


        const Modal = getComponent("Modal")

        return <div className="highlight-code">
            <div className="highlight-code-inner" key="small" onMouseEnter={this.copyHoverOn} onMouseLeave={this.copyHoverOff}>
                <SyntaxHighlighter
                    onClick={this.copyToClipboard}
                    showLineNumbers={showLineNumbers}
                    wrapLines={true}
                    useInlineStyles={false}
                >
                    {value}
                </SyntaxHighlighter>
                <div className="highlight-button-wrapper">
                    {copyHover && !showModal &&
                        <h1 className="highlight-hover">{copied ? "Copied" : "Click to Copy"}</h1>
                    }
                    {bigHover && !showModal &&
                        <h1 className="highlight-hover">Click to Maximize</h1>
                    }
                    {max && <CropFree className="highlight-button" onClick={this.showModal} onMouseEnter={this.bigHoverOn} onMouseLeave={this.bigHoverOff} />}
                    <FilterNone className="highlight-button" onClick={this.copyToClipboard} />
                </div>
            </div>
            {max && showModal && <Modal
                handleClose={this.hideModal}
            >
                <div className="highlight-code-inner big" key="big" onMouseEnter={this.copyHoverOn} onMouseLeave={this.copyHoverOff}>
                    <SyntaxHighlighter
                        onClick={this.copyToClipboard}
                        showLineNumbers={showLineNumbers}
                        wrapLines={true}
                        useInlineStyles={false}
                    >
                        {value}
                    </SyntaxHighlighter>
                    <div className="highlight-button-wrapper">
                        {copyHover && 
                            <h1 className="highlight-hover">{copied ? "Copied" : "Click to Copy"}</h1>
                        }
                        {bigHover && 
                            <h1 className="highlight-hover">Click or press Esc to Minimize</h1>
                        }
                        {max && <CropFree className="highlight-button" onClick={this.hideModal} onMouseEnter={this.bigHoverOn} onMouseLeave={this.bigHoverOff} />}
                        <FilterNone className="highlight-button" onClick={this.copyToClipboard} />
                    </div>
                </div>
            </Modal>}
        </div>
    }
}

