import React, { Component } from "react"
import PropTypes from "prop-types"
import Im from "immutable"
import ImPropTypes from "react-immutable-proptypes"

// More readable, just iterate over maps, only
const eachMap = (iterable, fn) => iterable.valueSeq().filter(Im.Map.isMap).map(fn)

export default class Parameters extends Component {

  constructor(props) {
    super(props)
    this.state = {
      callbackVisible: false,
      parametersVisible: true
    }
  }

  static propTypes = {
    parameters: ImPropTypes.list.isRequired,
    specActions: PropTypes.object.isRequired,
    getComponent: PropTypes.func.isRequired,
    specSelectors: PropTypes.object.isRequired,
    oas3Selectors: PropTypes.object.isRequired,
    fn: PropTypes.object.isRequired,
    isExecute: PropTypes.bool,
    onChangeKey: PropTypes.array,
  }


  static defaultProps = {
    isExecute: false,
    onChangeKey: [],
  }

  onChange = (param, value, isXml) => {
    let {
      specActions: { changeParamByIdentity },
      onChangeKey,
    } = this.props

    changeParamByIdentity(onChangeKey, param, value, isXml)
  }

  toggleTab = (tab) => {
    if (tab === "parameters") {
      return this.setState({
        parametersVisible: true,
        callbackVisible: false
      })
    }
  }

  render() {

    let {
      parameters,
      isExecute,

      fn,
      getComponent,
      specSelectors,
      onChangeKey,
      oas3Selectors,
    } = this.props

    const ParameterRow = getComponent("parameterRow")

    const isOAS3 = specSelectors.isOAS3()

    // variables, that parameters can have => will only be displayed, if at least one parameter in parameters got this value
    let vars = {"name": true, "type": true, "description": false, "example": false, "constraints": false}

    parameters.map(parameter => {
        if (parameter.get("example")) {
            vars.example = true
        }
        if (parameter.get("constraints")) {
            vars.constraints = true
        }
        if (parameter.get("description")) {
          vars.description = true
        }
    })

    return (
      <div className="opblock-section">
        <div className="opblock-section-header">
          {isOAS3 ? (
            <div className="tab-header">
              <div onClick={() => this.toggleTab("parameters")} className={`tab-item ${this.state.parametersVisible && "active"}`}>
                <h4 className="opblock-title"><span>Parameters</span></h4>
              </div>
            </div>
          ) : (
              <div className="tab-header">
                <h4 className="opblock-title">Parameters</h4>
              </div>
            )}
        </div>
        {this.state.parametersVisible && <div className="oplock-section-body">
          {!parameters.count() ? <div className="opblock-description-wrapper"><p>No parameters</p></div> :
            <div className="table-container">
              <table className="table">
                <thead>
                  <tr>
                    {vars.name && <th className="col_header parameters-col_name">name</th>}
                    {vars.type && <th className="col_header parameters-col_type">type</th>}
                    {vars.description && <th className="col_header parameters-col_description">description</th>}
                    {vars.example && <th className="col_header parameters-col_example">example</th>}
                    {vars.constraints && <th className="col-header parameters-col_constraints">constraints</th>}
                  </tr>
                </thead>
                <tbody>
                  {
                    eachMap(parameters, (parameter, i) => (
                      <ParameterRow
                        fn={fn}
                        getComponent={getComponent}
                        rawParam={parameter}
                        param={specSelectors.parameterWithMetaByIdentity(onChangeKey, parameter)}
                        key={`${parameter.get("in")}.${parameter.get("name")}`}
                        onChange={this.onChange}
                        specSelectors={specSelectors}
                        oas3Selectors={oas3Selectors}
                        pathMethod={onChangeKey}
                        isExecute={isExecute} 
                        vars={vars}/>
                    )).toArray()
                  }
                </tbody>
              </table>
            </div>
          }
        </div>}
      </div>
    )
  }
}
