import React from "react"
import PropTypes from "prop-types"

export default class TryItOutButton extends React.Component {

    static propTypes = {
        onTryoutClick: PropTypes.func,
        onCancelClick: PropTypes.func,
        enabled: PropTypes.bool, // Try it out is enabled, ie: the user has access to the form
    };

    static defaultProps = {
        onTryoutClick: Function.prototype,
        onCancelClick: Function.prototype,
        enabled: false,
    };

    constructor(props) {
        super(props)
        this.state = {
            error: false
        }
    }

    render() {
        const { onTryoutClick, onCancelClick, enabled, isAuthorized } = this.props

        const title = "Try"

        return (
            <div className="try-out">
                {
                    enabled ? <button className="btn cancel" onClick={onCancelClick}>Cancel</button>
                        : isAuthorized ? <button className="btn try-out__btn" onClick={onTryoutClick}>{title}</button>
                            : <div className="try-out_inner">
                                <button
                                    className={this.state.error ? "btn try-out__btn unauthorized error" : "btn try-out__btn unauthorized"}
                                    onClick={() => this.setState({ error: true })}
                                    onAnimationEnd={() => this.setState({ error: false })}>
                                    {title}</button>
                                <div className="text-unauthorized">please authenticate first</div>
                            </div>
                }
            </div>
        )
    }
}
