import React from "react"
import PropTypes from "prop-types"

export default class AuthWrapper extends React.Component {

    static propTypes = {
        fn: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,
        authSelectors: PropTypes.object.isRequired,
        specSelectors: PropTypes.object.isRequired,
        specActions: PropTypes.object.isRequired,
        errSelectors: PropTypes.object.isRequired,
        authActions: PropTypes.object.isRequired,
    }

    authorize() {
        // indirect: Auths and System URL are listening to this component (on click)
        // then they call their confirm function
    }

    render() {
        let { 
            authSelectors, 
            authActions,
            getComponent, 
            errSelectors, 
            specSelectors, 
            specActions, 
            fn: { AST = {} } 
        } = this.props
        
        let definitions = authSelectors.definitionsToAuthorize()
        const Auths = getComponent("auths")
        const BaseURL = getComponent("BaseURL")
        const Button = getComponent("Button")

        return (
            <div className="auth-wrapper">
                {
                    definitions.valueSeq().map((definition, key) => {
                        return <Auths key={key}
                            AST={AST}
                            definitions={definition}
                            getComponent={getComponent}
                            errSelectors={errSelectors}
                            authSelectors={authSelectors}
                            authActions={authActions}
                            specSelectors={specSelectors} />
                    })
                }
                <BaseURL 
                    specActions={specActions} 
                    specSelectors={specSelectors} 
                    getComponent={getComponent} 
                    authActions={authActions} 
                    authSelectors={authSelectors} 
                />
                <div className="auth-btn-wrapper">
                    {
                        <Button className="btn auth authorize" onClick={this.authorize} id="auth-button" >
                            Authorize
                        </Button>
                    }
                </div>
            </div>
        )
    }
}
