import React from "react"
import PropTypes from "prop-types"
import { ExpandLess, ExpandMore } from '@material-ui/icons';

export default class Url extends React.Component {
  static propTypes = {
    url: PropTypes.string.isRequired,
  }

  constructor(props) {
    super(props)
    this.state = {
      open: true
    }
  }

  render() {
    let { url } = this.props

    let {
      open
    } = this.state

    return (
      <div>
        <div className="live-response header close" onClick={() => this.setState((state) => ({ open: !state.open }))}>
          <div className="icon">
            {open ? <ExpandLess /> : <ExpandMore />}
          </div>
          <h1 className="title">Request URL</h1>
        </div>

        {open && <div className="live-response body">
          <div className="highlight-code">
            <div className="highlight-code-inner" key="small">
              <span>{url}</span>
            </div>
          </div>
        </div>}

      </div>
    )
  }

}
