import React, { PureComponent } from "react"
import PropTypes from "prop-types"
import { getList } from "core/utils"
import { escapeDeepLinkPath, getSampleSchema } from "core/utils"
import { Iterable, List, Map } from "immutable"
import ImPropTypes from "react-immutable-proptypes"
import toString from "lodash/toString"
import MutationObserver from "./MutationObserver"

export default class Operation extends PureComponent {
    static propTypes = {
        specPath: ImPropTypes.list.isRequired,
        operation: PropTypes.instanceOf(Iterable).isRequired,
        summary: PropTypes.string,
        response: PropTypes.instanceOf(Iterable),
        request: PropTypes.instanceOf(Iterable),

        onTryoutClick: PropTypes.func.isRequired,
        onCancelClick: PropTypes.func.isRequired,
        onExecute: PropTypes.func.isRequired,

        getComponent: PropTypes.func.isRequired,
        getConfigs: PropTypes.func.isRequired,
        authActions: PropTypes.object,
        authSelectors: PropTypes.object,
        specActions: PropTypes.object.isRequired,
        specSelectors: PropTypes.object.isRequired,
        oas3Selectors: PropTypes.object.isRequired,
        layoutActions: PropTypes.object.isRequired,
        layoutSelectors: PropTypes.object.isRequired,
        fn: PropTypes.object.isRequired
    }

    static defaultProps = {
        operation: null,
        response: null,
        request: null,
        specPath: List(),
        summary: ""
    }

    toggleShownWrapper() {
        let {
            i,
            prevShownKey,
            toggleShown,
            isShown,
            processedTag
        } = this.props

        if (isShown) {
            return
        }

        let operationProps = this.props.operation

        let {
            operationId,
        } = operationProps.toJS()

        let isShownKey = ["operations", processedTag, operationId, i]
        const thisElem = document.getElementById(isShownKey.join("-") + "-watch")
        const parent = document.getElementById(prevShownKey + "-watch")

        const inViewport = this.isInViewport(thisElem)
        const parentOpen = parent ? parent.classList.contains("op-open") : true
        const parentInViewport = parent ? this.isInViewport(parent) : true
        if ((!parentInViewport || parentOpen) && inViewport) {
            toggleShown()
        }
    }

    componentDidMount() {
        let { prevShownKey, isShown, processedTag } = this.props
        if (isShown) {
            return
        }
        if (prevShownKey) {
            this.classWatcher = new MutationObserver(document.getElementById(prevShownKey + "-watch"), "op-open", this.toggleShownWrapper.bind(this), this.classRemovedCallback.bind(this))
            this.classWatcher.observe()
        } else {
            this.toggleShownWrapper()
        }
        this.scrollStop(this.toggleShownWrapper)
    }

    componentWillUnmount() {
        if (this.classWatcher) {
            this.classWatcher.disconnect()
        }
        window.removeEventListener('scroll', this.scrollStop)
    }

    classRemovedCallback() {
        console.log("open removed")
    }

    scrollStop() {

        let callback = this.toggleShownWrapper.bind(this)

        // Make sure a valid callback was provided
        if (!callback || typeof callback !== 'function') return;
    
        // Setup scrolling variable
        var isScrolling;
    
        // Listen for scroll events
        window.addEventListener('scroll', function (event) {
    
            // Clear our timeout throughout the scroll
            window.clearTimeout(isScrolling);
    
            // Set a timeout to run after scrolling ends
            isScrolling = setTimeout(function() {
    
                // Run the callback
                callback();
    
            }, 66);
    
        }, false);
    
    }

    isInViewport(elem) {
        var bounding = elem.getBoundingClientRect();
        return (
            bounding.top >= 0 &&
            bounding.left >= 0 &&
            bounding.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
            bounding.right <= (window.innerWidth || document.documentElement.clientWidth)
        );
    }

    getBodyParameter(parameters, pathMethod) {
        let body
        parameters.forEach(parameter => {
            let current = this.props.specSelectors.parameterWithMetaByIdentity(pathMethod, parameter)
            if (current.get("name") === "body") {
                body = parameter
            }
        })
        return body
    }

    removeItem(list, item) {
        let newList = List()
        list.forEach(listItem => {
            if (listItem !== item) {
                newList = newList.push(listItem)
            }
        })
        return newList
    }

    getExampleResponses(responses, pathMethod) {
        let exampleResponses = []

        let { inferSchema } = this.props.fn

        responses.entrySeq().forEach(([code, response]) => {
            var sampleResponse
            var schema, specPathWithPossibleSchema
            var specPath = this.props.specPath.push("parameters").push(code)
            const activeContentType = this.props.specSelectors.currentProducesFor(pathMethod)
            const activeMediaType = response.getIn(["content", activeContentType], Map({}))
            const examplesForMediaType = activeMediaType.get("examples", null)

            // Goal: find a schema value for `schema`
            if (this.props.specSelectors.isOAS3()) {
                const oas3SchemaForContentType = activeMediaType.get("schema")

                schema = oas3SchemaForContentType ? inferSchema(oas3SchemaForContentType.toJS()) : null
                specPathWithPossibleSchema = oas3SchemaForContentType ? List(["content", activeContentType, "schema"]) : specPath
            } else {
                schema = response.get("schema")
                specPathWithPossibleSchema = response.has("schema") ? specPath.push("schema") : specPath
            }

            // Goal: find an example value for `sampleResponse`
            if (this.props.specSelectors.isOAS3()) {
                const oas3SchemaForContentType = activeMediaType.get("schema", Map({}))

                if (examplesForMediaType) {
                    const targetExamplesKey = this.getTargetExamplesKey()
                    const targetExample = examplesForMediaType.get(targetExamplesKey, Map({}))
                    sampleResponse = stringify(targetExample.get("value"))
                } else if (activeMediaType.get("example") !== undefined) {
                    // use the example key's value
                    sampleResponse = stringify(activeMediaType.get("example"))
                } else {
                    // use an example value generated based on the schema
                    sampleResponse = getSampleSchema(oas3SchemaForContentType.toJS(), activeContentType, {
                        includeReadOnly: true
                    })
                }
            } else {
                if (response.getIn(["examples", activeContentType])) {
                    sampleResponse = response.getIn(["examples", activeContentType])
                } else {
                    sampleResponse = schema ? getSampleSchema(
                        schema.toJS(),
                        activeContentType,
                        {
                            includeReadOnly: true,
                            includeWriteOnly: true // writeOnly has no filtering effect in swagger 2.0
                        }
                    ) : null
                }
            }

            if (sampleResponse) {
                exampleResponses.push([code, sampleResponse])
            }

        })

        return exampleResponses
    }


    render() {
        let {
            specPath,
            response,
            onTryoutClick,
            onCancelClick,
            onExecute,
            toggleShown,
            fn,
            getComponent,
            getConfigs,
            specActions,
            specSelectors,
            authActions,
            authSelectors,
            oas3Selectors,
            i,
            processedTag,
            prevShownKey
        } = this.props
        let operationProps = this.props.operation

        let {
            deprecated,
            isShown,
            path,
            method,
            op,
            tag,
            operationId,
            allowTryItOut,
            displayRequestDuration,
            tryItOutEnabled,
            executeInProgress,
            showSummary,
            summary,
            isAuthorized
        } = operationProps.toJS()

        let {
            description,
            summary: resolvedSummary
        } = op

        let security = operationProps.get("security")
        isAuthorized = (authSelectors.systemUrl() !== "") && (isAuthorized || (security && security.count() === 0))

        let operation = operationProps.getIn(["op"])
        let responses = operation.get("responses")
        let parameters = getList(operation, ["parameters"])
        // seperate body and other parameters
        let requestBody = this.getBodyParameter(parameters, [path, method])
        parameters = this.removeItem(parameters, requestBody)

        // find response with example
        let exampleResponses = responses ? this.getExampleResponses(responses, [path, method]) : null


        // check, if Try Out Button should work        
        let definitions = authSelectors.definitionsToAuthorize()
        let authAvailable = definitions && definitions.size != 0


        const Responses = getComponent("responses")
        const Parameters = getComponent("parameters")
        const RequestBody = getComponent("RequestBody")
        const Execute = getComponent("execute")
        const Clear = getComponent("clear")
        const Collapse = getComponent("Collapse")
        const Markdown = getComponent("Markdown")
        const OperationSummary = getComponent("OperationSummary")
        const TryItOutButton = getComponent("TryItOutButton")
        const ResponseExample = getComponent("ResponseExample")
        const ServerResponse = getComponent("ServerResponse")
        const ResponseTypeSelector = getComponent("ResponseTypeSelector")


        // Merge in Live Response
        if (responses && response && response.size > 0) {
            let notDocumented = !responses.get(String(response.get("status"))) && !responses.get("default")
            response = response.set("notDocumented", notDocumented)
        }

        let onChangeKey = [path, method] // Used to add values to _this_ operation ( indexed by path and method )

        let isShownKey = ["operations", processedTag, operationId, i]

        return (
            <div className={deprecated ? "opblock opblock-deprecated" : isShown ? `opblock opblock-${method} is-open` : `opblock opblock-${method}`} id={isShownKey.join("-")} >
                <OperationSummary
                    operationProps={operationProps}
                    getComponent={getComponent}
                    authActions={authActions}
                    authSelectors={authSelectors}
                    specPath={specPath}
                    toggleShown={toggleShown}
                    isShown={isShown}
                />
                <div className={(isShown && operation && operation.size != 0 && operation != null) ? "op-open" : "op-closed"} id={isShownKey.join("-") + "-watch"}>
                    <Collapse isOpened={isShown}>
                        <div className="opblock-body">
                            {!((operation && operation.size != 0) || operation === null) &&
                                <div className="loading-container"><div className="loading-small"></div></div>
                            }
                            {deprecated && <h4 className="opblock-title_normal"> Warning: Deprecated</h4>}
                            {operation && operation.size != 0 && (description || showSummary) &&
                                <div className="opblock-description-wrapper">
                                    <div className="opblock-description">
                                        {description ? <Markdown source={description} />
                                            : (showSummary && <Markdown source={toString(resolvedSummary || summary)} />)
                                        }
                                    </div>
                                </div>
                            }

                            {requestBody &&
                                <RequestBody
                                    specSelectors={specSelectors}
                                    getComponent={getComponent}
                                    fn={fn}
                                    specActions={specActions}
                                    oas3Selectors={oas3Selectors}
                                    consumes={specSelectors.consumesOptionsFor(onChangeKey)}

                                    body={specSelectors.parameterWithMetaByIdentity(onChangeKey, requestBody)}
                                    rawBody={requestBody}
                                    pathMethod={onChangeKey}
                                    specPath={specPath}
                                    isExecute={tryItOutEnabled && allowTryItOut}
                                />
                            }

                            {operation && operation.size != 0 && parameters.size != 0 &&
                                <Parameters
                                    parameters={parameters}
                                    onChangeKey={onChangeKey}
                                    isExecute={tryItOutEnabled && allowTryItOut}

                                    fn={fn}
                                    getComponent={getComponent}
                                    specActions={specActions}
                                    specSelectors={specSelectors}
                                    oas3Selectors={oas3Selectors}
                                />
                            }
                            {authAvailable && allowTryItOut && operation && operation.size != 0 &&
                                <div className="btn-group">
                                    {tryItOutEnabled &&
                                        <Execute
                                            operation={operation}
                                            specActions={specActions}
                                            specSelectors={specSelectors}
                                            path={path}
                                            method={method}
                                            onExecute={onExecute} />
                                    }
                                    {tryItOutEnabled && response &&
                                        <Clear
                                            specActions={specActions}
                                            path={path}
                                            method={method} />
                                    }
                                    <TryItOutButton
                                        enabled={tryItOutEnabled}
                                        onCancelClick={onCancelClick}
                                        onTryoutClick={onTryoutClick}
                                        isAuthorized={isAuthorized}
                                    />
                                    {!specSelectors.isOAS3() &&
                                        <ResponseTypeSelector
                                            produces={specSelectors.producesOptionsFor([path, method])}
                                            producesValue={specSelectors.currentProducesFor([path, method])}
                                            path={path}
                                            method={method}
                                            getComponent={getComponent}
                                            specActions={specActions}
                                        />
                                    }
                                </div>
                            }

                            {executeInProgress &&
                                <div className="opblock-section">
                                    <div className="opblock-section-header">
                                        <h4>Server Response</h4>
                                    </div>
                                    <div className="live-response wrapper">
                                        <div className="loading-container"><div className="loading-small"></div></div>
                                    </div>
                                </div>}

                            {!executeInProgress && exampleResponses && exampleResponses.length != 0 && !response &&
                                <ResponseExample
                                    responses={exampleResponses}
                                    getComponent={getComponent}
                                />
                            }
                            {!executeInProgress && response &&
                                <ServerResponse
                                    tryItOutResponse={response}
                                    getComponent={getComponent}
                                    getConfigs={getConfigs}
                                    specSelectors={specSelectors}
                                    displayRequestDuration={displayRequestDuration}
                                    path={path}
                                    method={method}
                                />
                            }
                            {responses &&
                                <Responses
                                    responses={responses}
                                    tryItOutResponse={response}
                                    getComponent={getComponent}
                                />
                            }

                        </div>
                    </Collapse>
                </div>
            </div>
        )
    }

}
