import React from "react"
import PropTypes from "prop-types"
import { ExpandLess, ExpandMore, CheckBox, CheckBoxOutlineBlank } from '@material-ui/icons';

export default class Drawer extends React.Component {

    static propTypes = {
        specSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,
        fn: PropTypes.object.isRequired,
        authSelectors: PropTypes.object.isRequired,
        errSelectors: PropTypes.object.isRequired,
        authActions: PropTypes.object.isRequired,
    }

    constructor(props) {
        super(props)

        // position of window
        this.state = {
            position: 0,
            open: true,
            authorized: false
        }

        this.listenToScroll = this.listenToScroll.bind(this)
        this.toggleAuthorizedOpen = this.toggleAuthorizedOpen.bind(this)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.listenToScroll)
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.listenToScroll)
    }

    componentWillReceiveProps() {
        let { authSelectors } = this.props
        let { authorized, open } = this.state

        let authNew = ((authSelectors.authorized().size == authSelectors.definitionsToAuthorize().size) && (authSelectors.systemUrl() !== ""))
        let openNew = open ? !(authNew && (authorized != authNew)) : false
        this.setState({
            authorized: authNew,
            open: openNew
        })
    }

    listenToScroll() {
        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop

        // Position of Element, window is at
        var minPos = -1
        this.props.specSelectors.taggedOperations().map((tagObj, tag) => {
            let processedTag = tag;
            while (processedTag.search(" ") !== -1) {
                processedTag = processedTag.replace(" ", "_");
            }
            processedTag = processedTag.replace(/[^0-9a-zA-Z_]/g, "")
            var offset = document.getElementById("operations-tag-wrapper-" + processedTag) ? document.getElementById("operations-tag-wrapper-" + processedTag).offsetTop : 0
            if (minPos === -1) {
                minPos = offset
            } else if ((scrollTop - offset > -(window.innerHeight / 2)) && (offset > minPos)) {
                minPos = offset
            }
        })

        var offset = document.getElementById("models") ? document.getElementById("models").offsetTop : 0
        if (minPos === -1) {
            minPos = offset
        } else if ((scrollTop - offset > -(window.innerHeight / 2)) && (offset > minPos)) {
            minPos = offset
        }

        this.setState({
            position: minPos
        })
    }

    toggleAuthorizedOpen() {
        if (this.state.authorized) {
            this.setState((state) => ({
                open: !state.open
            }))
        }
    }

    render() {
        let {
            specSelectors,
            getComponent,
            fn,
            authSelectors,
            authActions,
            errSelectors,
            specActions
        } = this.props

        // every operation
        let taggedOps = specSelectors.taggedOperations()

        const NavLink = getComponent("NavLink")
        const ModelNavLink = getComponent("ModelNavLink")
        const Authorization = getComponent("AuthWrapper")
        const NoAuth = getComponent("NoAuth")

        let definitions = authSelectors.definitionsToAuthorize()
        let authAvailable = definitions && definitions.size != 0

        let {
            position,
            open,
            authorized
        } = this.state

        return <div className="navigation-wrapper" id="navigation-wrapper">
            {authAvailable
                ? <div className="navigation-section">
                    <div className="header" onClick={this.toggleAuthorizedOpen}>
                        {authorized ? <CheckBox className="auth-item" /> : <CheckBoxOutlineBlank className="auth-item" />}
                        <h2 className="navigation-headline">Authorization</h2>
                        {authorized &&
                            (open ? <ExpandLess /> : <ExpandMore />)
                        }
                    </div>
                    <div className={(!authorized || open) ? "body" : "body closed"}>
                        <Authorization
                            fn={fn}
                            getComponent={getComponent}
                            authSelectors={authSelectors}
                            specSelectors={specSelectors}
                            errSelectors={errSelectors}
                            authActions={authActions}
                            authSelectors={authSelectors}
                            specActions={specActions}
                        />
                    </div>
                </div>
                : <div className="navigation-section">
                    <NoAuth />
                </div>

            }

            <div className="navigation-section">
                <div className="header">
                    <h2 className="navigation-headline">API Reference</h2>
                </div>
                <div className="body">
                    {taggedOps.map((tagObj, tag) => {
                        let processedTag = tag;
                        while (processedTag.search(" ") !== -1) {
                            processedTag = processedTag.replace(" ", "_");
                        }
                        processedTag = processedTag.replace(/[^0-9a-zA-Z_]/g, "")
                        return <NavLink
                            key={tag}
                            tagObj={tagObj}
                            tag={tag}
                            processedTag={processedTag}
                            getComponent={getComponent}
                            specSelectors={specSelectors}
                            position={position}
                        />
                    }).toArray()}
                    <ModelNavLink position={position} />
                </div>
            </div>
        </div>
    }
}