import React, { PureComponent } from "react"
import PropTypes from "prop-types"
import { fromJS } from "immutable"


export default class BodyTypeSelector extends PureComponent {

    static propTypes = {
        consumes: PropTypes.object,
        getComponent: PropTypes.func.isRequired,
        specSelectors: PropTypes.object.isRequired,
        pathMethod: PropTypes.array.isRequired,
        specActions: PropTypes.object.isRequired,
    };

    static defaultProp = {
        consumes: fromJS(["application/json"]),
    };

    onChangeConsumesWrapper = (val) => {
        let {
            specActions: { changeConsumesValue },
            pathMethod
        } = this.props

        changeConsumesValue(pathMethod, val)
    }


    render() {
        let {
            specSelectors,
            pathMethod,

            getComponent,
        } = this.props

        const ContentType = getComponent("contentType")
        // for domains where specSelectors not passed
        let consumesValue = specSelectors.contentTypeValues(pathMethod).get("requestContentType")
        let consumes = this.props.consumes && this.props.consumes.size ? this.props.consumes : BodyTypeSelector.defaultProp.consumes

        return (
            <div className="type-chooser">
                <h1>Parameter content type</h1>
                <ContentType 
                    value={consumesValue} 
                    contentTypes={consumes} 
                    onChange={this.onChangeConsumesWrapper} 
                    className="body-param-content-type" 
                />
            </div>
        )

    }
}
