import WrapOperation from "./Operation"
import WrapSummary from "./Summary"
import WrapSummaryPath from "./SummaryPath"
import WrapParameters from "./parameters/Parameters"
import WrapParameterRow from "./parameters/ParameterRow"
import WrapTryButton from "./TryButton"
import WrapRequestBody from "./parameters/RequestBody"
import RequestSample from "./parameters/RequestSample"
import RequestSampleWrapper from "./parameters/RequestSampleWrapper"
import WrapResponses from "./responses/Responses"
import WrapResponse from "./responses/Response"
import ResponseExample from "./responses/ResponseExample"
import WrapHighlightCode from "./HightlightCode"
import ServerResponse from "./liveResponse/ServerResponse"
import Modal from "./Modal"
import WrapLiveResponse from "./liveResponse/LiveResponse"
import WrapCurl from "./liveResponse/Curl"
import Url from "./liveResponse/Url"
import WrapResponseBody from "./liveResponse/ResponseBody"
import WrapExecute from "./ExecuteButton"
import WrapClear from "./ClearButton"
import ResponseTypeSelector from "./responses/ResponseTypeSelector"
import BodyTypeSelector from "./parameters/BodyTypeSelector"
import TextArea from "./parameters/TextArea"
import JsonSchemaForm from "./parameters/JsonSchemaForm"
import Headers from "./liveResponse/Headers"
import OperationContainer from "./OperationContainer"
import Operations from "./Operations"
import OperationTag from "./OperationTag"

export default function() {
    return {
        components: {
            RequestSample,
            ResponseExample,
            RequestSampleWrapper,
            ServerResponse,
            Modal,
            Url,
            ResponseTypeSelector,
            BodyTypeSelector,
            Headers
        },
        wrapComponents: {
            
            OperationContainer: (Original, { React }) => (props) => {
                return <OperationContainer {...props} />
            },
            operation: (Original, { React }) => (props) => {
                return <WrapOperation {...props} />
            },
            OperationSummary: (Original, { React }) => (props) => {
                return <WrapSummary {...props} />
            },
            OperationSummaryPath: (Original, { React }) => (props) => {
                return <WrapSummaryPath {...props} />
            },
            parameters: (Original, { React }) => (props) => {
                return <WrapParameters {...props} />
            },
            parameterRow: (Original, { React }) => (props) => {
                return <WrapParameterRow {...props} />
            },
            TryItOutButton: (Original, { React }) => (props) => {
                return <WrapTryButton {...props} />
            },
            RequestBody: (Original, { React }) => (props) => {
                return <WrapRequestBody {...props} />
            },
            responses: (Original, { React }) => (props) => {
                return <WrapResponses {...props} />
            },
            response: (Original, { React }) => (props) => {
                return <WrapResponse {...props} />
            },
            liveResponse: (Original, { React }) => (props) => {
                return <WrapLiveResponse {...props} />
            },
            curl: (Original, { React }) => (props) => {
                return <WrapCurl {...props} />
            },
            responseBody: (Original, { React }) => (props) => {
                return <WrapResponseBody {...props} />
            },
            highlightCode: (Original, { React }) => (props) => {
                return <WrapHighlightCode {...props} />
            },
            clear: (Original, { React }) => (props) => {
                return <WrapClear {...props} />
            },
            execute: (Original, { React }) => (props) => {
                return <WrapExecute {...props} />
            },
            TextArea: (Original, { React }) => (props) => {
                return <TextArea {...props} />
            },
            JsonSchemaForm: (Original, { React }) => (props) => {
                return <JsonSchemaForm {...props} />
            },
            operations: (Original, { React }) => (props) => {
                return <Operations {...props} />
            },
            OperationTag: (Original, { React }) => (props) => {
                return <OperationTag {...props} />
            }
        }
    }
}