import React from "react"
import { fromJS, Iterable } from "immutable"
import PropTypes from "prop-types"

export default class ResponseTypeSelector extends React.Component {
  static propTypes = {
    produces: PropTypes.instanceOf(Iterable),
    producesValue: PropTypes.any,
    path: PropTypes.string.isRequired,
    method: PropTypes.string.isRequired,
    getComponent: PropTypes.func.isRequired,
    specActions: PropTypes.object.isRequired,
  }

  static defaultProps = {
    produces: fromJS(["application/json"]),
  }

	onChangeProducesWrapper = ( val ) => this.props.specActions.changeProducesValue([this.props.path, this.props.method], val)


  render() {
    let {
      getComponent,
      producesValue,
    } = this.props

    const ContentType = getComponent( "contentType" )

    let produces = this.props.produces && this.props.produces.size ? this.props.produces : ResponseTypeSelector.defaultProps.produces

    return (
      <div className="type-chooser response">
        <h1>Response Content Type: </h1>
        <ContentType 
          value={producesValue}
          onChange={this.onChangeProducesWrapper}
          contentTypes={produces}
          className="execute-content-type"
        />
      </div>
    )
  }
}
