import React from "react"
import PropTypes from "prop-types"
import Im from "immutable"
import { opId } from "swagger-client/es/helpers"

const SWAGGER2_OPERATION_METHODS = [
    "get", "put", "post", "delete", "options", "head", "patch"
]

const OAS3_OPERATION_METHODS = SWAGGER2_OPERATION_METHODS.concat(["trace"])


export default class Operations extends React.Component {

    static propTypes = {
        specSelectors: PropTypes.object.isRequired,
        specActions: PropTypes.object.isRequired,
        oas3Actions: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,
        layoutSelectors: PropTypes.object.isRequired,
        layoutActions: PropTypes.object.isRequired,
        authActions: PropTypes.object.isRequired,
        authSelectors: PropTypes.object.isRequired,
        getConfigs: PropTypes.func.isRequired,
        fn: PropTypes.func.isRequired
    };

    render() {
        let {
            specSelectors,
            getComponent,
            layoutSelectors,
            layoutActions,
            getConfigs,
            fn,
            specActions,
            authActions,
            authSelectors,
            oas3Actions,
            oas3Selectors,
        } = this.props

        let taggedOps = specSelectors.taggedOperations()

        const OperationContainer = getComponent("OperationContainer")
        const OperationTag = getComponent("OperationTag")

        let {
            maxDisplayedTags,
        } = getConfigs()

        let filter = layoutSelectors.currentFilter()

        if (filter) {
            if (filter !== true) {
                taggedOps = fn.opsFilter(taggedOps, filter)
            }
        }

        if (maxDisplayedTags && !isNaN(maxDisplayedTags) && maxDisplayedTags >= 0) {
            taggedOps = taggedOps.slice(0, maxDisplayedTags)
        }

        let prevOpShownKey = null // give to next operation, so it can check, whether the previous one is already open (to open itself)

        return (
            <div>
                {
                    taggedOps.map((tagObj, tag) => {
                        const operations = tagObj.get("operations")
                        let processedTag = tag;
                        while (processedTag.search(" ") !== -1) {
                            processedTag = processedTag.replace(" ", "_");
                        }
                        processedTag = processedTag.replace(/[^0-9a-zA-Z_]/g, "")
                        return <OperationTag
                            key={"operation-" + tag}
                            tagObj={tagObj}
                            tag={tag}
                            processedTag={processedTag}
                            layoutSelectors={layoutSelectors}
                            layoutActions={layoutActions}
                            getConfigs={getConfigs}
                            getComponent={getComponent}
                        > {
                                operations.map((op, i) => {

                                    const {
                                        docExpansion,
                                        deepLinking,
                                        displayOperationId,
                                        displayRequestDuration,
                                        supportedSubmitMethods
                                    } = getConfigs()

                                    const path = op.get("path")
                                    const method = op.get("method")
                                    const specPath = Im.List(["paths", path, method])

                                    const showSummary = layoutSelectors.showSummary()
                                    const operationId = op.getIn(["operation", "__originalOperationId"]) || op.getIn(["operation", "operationId"]) || opId(op.get("operation"), path, method) || op.get("id")
                                    const isShownKey = ["operations", processedTag, operationId, i]
                                    const isDeepLinkingEnabled = deepLinking && deepLinking !== "false"
                                    const allowTryItOut = supportedSubmitMethods.indexOf(method) >= 0 && (typeof allowTryItOut === "undefined" ?
                                        specSelectors.allowTryItOutFor(path, method) : allowTryItOut)
                                    const security = op.getIn(["operation", "security"]) || specSelectors.security()

                                    const prevShownKey = prevOpShownKey
                                    prevOpShownKey = isShownKey.join("-")

                                    // FIXME: (someday) this logic should probably be in a selector,
                                    // but doing so would require further opening up
                                    // selectors to the plugin system, to allow for dynamic
                                    // overriding of low-level selectors that other selectors
                                    // rely on. --KS, 12/17
                                    const validMethods = specSelectors.isOAS3() ?
                                        OAS3_OPERATION_METHODS : SWAGGER2_OPERATION_METHODS

                                    if (validMethods.indexOf(method) === -1) {
                                        return null
                                    }

                                    return <OperationContainer
                                        key={`${path}-${method}`}
                                        specPath={specPath}
                                        op={op}
                                        path={path}
                                        method={method}
                                        tag={tag}
                                        processedTag={processedTag}
                                        i={i}
                                        operationId={operationId}
                                        isDeepLinkingEnabled={isDeepLinkingEnabled}
                                        showSummary={showSummary}
                                        displayOperationId={displayOperationId}
                                        displayRequestDuration={displayRequestDuration}
                                        allowTryItOut={allowTryItOut}
                                        security={security}
                                        isAuthorized={authSelectors.isAuthorized(security)}
                                        isShown={layoutSelectors.isShown(isShownKey, docExpansion === "full")}
                                        jumpToKey={`paths.${path}.${method}`}
                                        response={specSelectors.responseFor(path, method)}
                                        request={specSelectors.requestFor(path, method)}
                                        authSelectors={authSelectors}
                                        specSelectors={specSelectors}
                                        specActions={specActions}
                                        getComponent={getComponent}
                                        getConfigs={getConfigs}
                                        layoutSelectors={layoutSelectors}
                                        layoutActions={layoutActions}
                                        authActions={authActions}
                                        oas3Actions={oas3Actions}
                                        oas3Selectors={oas3Selectors}
                                        fn={fn}
                                        prevShownKey={prevShownKey}
                                    />
                                }).toArray()
                            }
                        </OperationTag>
                    }).toArray()
                }
                {taggedOps.size < 1 ? <h3> No operations defined in spec! </h3> : null}
            </div>
        )
    }

}

Operations.propTypes = {
    layoutActions: PropTypes.object.isRequired,
    specSelectors: PropTypes.object.isRequired,
    specActions: PropTypes.object.isRequired,
    layoutSelectors: PropTypes.object.isRequired,
    getComponent: PropTypes.func.isRequired,
    fn: PropTypes.object.isRequired
}
