import React, { PureComponent } from "react"
import PropTypes from "prop-types"
import { Iterable } from "immutable"
import { createDeepLinkPath } from "core/utils"
import ImPropTypes from "react-immutable-proptypes"

export default class OperationSummaryPath extends PureComponent{

  static propTypes = {
    specPath: ImPropTypes.list.isRequired,
    operationProps: PropTypes.instanceOf(Iterable).isRequired,
    getComponent: PropTypes.func.isRequired,
  }

  onCopyCapture = (e) => {
    // strips injected zero-width spaces (`\u200b`) from copied content
    e.clipboardData.setData("text/plain", this.props.operationProps.get("path"))
    e.preventDefault()
  }

  insert = (arr, index, newItem) => { return [
    // part of the array before the specified index
    ...arr.slice(0, index),
    // inserted item
    newItem,
    // part of the array after the specified index
    ...arr.slice(index)
  ]}

  render(){
    let {
      getComponent,
      operationProps,
      toggleShown
    } = this.props


    let {
      deprecated,
      path,
    } = operationProps.toJS()

    path = path.replace(/\//g, "\u200b/")

    var parameterRegex = new RegExp("(.*)({.*})(.*)")
    var result = parameterRegex.exec(path);
    var parsedPath = result ? (result[3] == "" ? [] : [ result[3] ]) : null
    
    while (result) {
        parsedPath = this.insert(
            result[3] == "" ? parsedPath : this.insert(parsedPath, 0, result[3])
            , 0, result[2])
        path = result[1]
        result = parameterRegex.exec(result[1])
    }

    parsedPath = parsedPath ? this.insert(parsedPath, 0, path) : [path]

    return(
      <div className={ deprecated ? "opblock-summary-path__deprecated" : "opblock-summary-path" } onClick={toggleShown}>
        <span>{parsedPath.map((pathPart, i) => {
            return <span className={pathPart[0] != "{" ? "normal" : "color"} key={pathPart + i.toString()}>{ pathPart }</span>
        })}</span>
      </div>

    )
  }
}
