import StandaloneLayout from "./layout"
import TopbarPlugin from "plugins/topbar"
import ConfigsPlugin from "corePlugins/configs"
import OperationPlugin from "plugins/operation"
import NavigationPlugin from "plugins/navigation"
import LayoutPlugin from "plugins/baseLayout"
import ModelPlugin from "plugins/models"

// the Standalone preset

export default [
  TopbarPlugin,
  ConfigsPlugin,
  OperationPlugin,
  NavigationPlugin,
  LayoutPlugin,
  ModelPlugin,
  () => {
    return {
      components: { StandaloneLayout }
    }
  },  
]
