import Drawer from "./Drawer"
import NavLink from "./nav/NavLink"
import NavSubLink from "./nav/NavSubLink"
import ModelNavLink from "./nav/ModelNavLink"
import AuthWrapper from "./auth/AuthWrapper"
import Auths from "./auth/Auths"
import AuthItem from "./auth/AuthItem"
import ApiKey from "./auth/ApiKey"
import BasicAuth from "./auth/BasicAuth"
import BaseURL from "./auth/BaseURL"
import * as wrapActions from "./spec/wrapActions"
import reducers from "./auth/statePlugins/reducers"
import * as actions from "./auth/statePlugins/actions"
import * as selectors from "./auth/statePlugins/selectors"
import NoAuth from "./auth/NoAuth"

export default function (system) {
    return {
        statePlugins: {
            spec: {
                wrapActions
            },
            auth: {
                reducers,
                actions,
                selectors
            },
        },
        components: {
            Drawer,
            NavLink,
            ModelNavLink,
            NavSubLink,
            AuthWrapper,
            BaseURL,
            NoAuth
        },
        wrapComponents: {
            auths: (Original, { React }) => (props) => {
                return <Auths {...props} />
            },
            AuthItem: (Original, { React }) => (props) => {
                return <AuthItem {...props} />
            },
            apiKeyAuth: (Original, { React }) => (props) => {
                return <ApiKey {...props} />
            },
            basicAuth: (Original, { React }) => (props) => {
                return <BasicAuth {...props} />
            },
        }
    }
}