import React from "react"
import PropTypes from "prop-types"
import curlify from "core/curlify"
import { ExpandLess, ExpandMore } from '@material-ui/icons';

export default class Curl extends React.Component {
  static propTypes = {
    request: PropTypes.object.isRequired,
  }

  constructor(props) {
    super(props)
    this.state = {
      open: true
    }
  }

  render() {
    let { request } = this.props
    let curl = curlify(request)

    let {
      open
    } = this.state

    return (
      <div>
        {<div className="live-response header close" onClick={() => this.setState((state) => ({ open: !state.open }))}>
          <div className="icon">
            {open ? <ExpandLess /> : <ExpandMore />}
          </div>
          <h1 className="title">Curl</h1>
        </div>}
        {open && <div className="live-response body">
          <div className="highlight-code">
            <div className="highlight-code-inner" key="small">
              <span>{curl}</span>
            </div>
          </div>
        </div>}
      </div>
    )
  }

}
