import React from "react"
import PropTypes from "prop-types"
import ImPropTypes from "react-immutable-proptypes"
import { Info, CheckBox, CheckBoxOutlineBlank, DonutSmall } from "@material-ui/icons"


export default class BasicAuth extends React.Component {
    static propTypes = {
        authorized: PropTypes.object,
        getComponent: PropTypes.func.isRequired,
        schema: PropTypes.object.isRequired,
        onChange: PropTypes.func.isRequired,
        authActions: PropTypes.object.isRequired,
    }

    constructor(props, context) {
        super(props, context)
        let { schema, name } = this.props

        let value = this.getValue()
        let username = value.username

        this.state = {
            name: name,
            schema: schema,
            value: !username ? {} : {
                username: username
            },
            showInfo: false,
            errorUsername: false,
            errorPassword: false
        }

        this.infoToggle = this.infoToggle.bind(this)
        this.confirm = this.confirm.bind(this)
    }

    componentDidMount() {
        const authButton = document.getElementById("auth-button")
        authButton.addEventListener('click', () => this.confirm())
    }

    componentWillUnmount() {
        const authButton = document.getElementById("auth-button")
        authButton.removeEventListener('click', () => this.confirm())
    }

    infoToggle() {
        this.setState((state) => ({
            showInfo: !state.showInfo
        }))
    }

    getValue() {
        let { authorized, name } = this.props

        return authorized && authorized.getIn([name, "value"]) || {}
    }

    onChange = (e) => {
        let { value, name } = e.target

        let newValue = this.state.value
        newValue[name] = value

        this.setState({ value: newValue })
    }

    keyPress = (e) => {
        if (e.keyCode == 13) {
            let { name } = e.target
            if (name === "username") {
                this.state.value.password && this.confirm()
                if (this.state.value.username) {
                    document.getElementById("auth-username").blur()
                    document.getElementById("auth-password").focus()
                } else {
                    this.setState({errorUsername: true})
                }
            } else if (name === "password") {
                this.confirm()
                if (this.state.value.password) {
                    document.getElementById("auth-password").blur()
                }
            }

        }
    }

    confirm() {
        let { onChange, submit, logout, authSelectors } = this.props
        
        onChange(this.state)

        let { value } = this.state
        let authorizedUsername = value.username ? true : false
        let authorizedPassword = value.password ? true : false
        
        this.setState({
            errorUsername: !authorizedUsername,
            errorPassword: !authorizedPassword
        })

        if (authorizedUsername && authorizedPassword) {
            submit()
        } else if(authSelectors.authorized().get("BasicAuth")) {
            logout()
        }

        return authorizedPassword && authorizedUsername
    }

    render() {
        let { schema, getComponent, name, errSelectors } = this.props
        const Input = getComponent("Input")
        const AuthError = getComponent("authError")

        let errors = errSelectors.allErrors().filter(err => err.get("authId") === name)
        let description = schema.get("description")

        let { errorUsername, errorPassword, showInfo, value } = this.state
        let username = value.username
        let password = value.password

        return (

            <div className="auth-simple" id="auth-simple-basic">
                <div className="basic" >
                    <Input
                        type="text"
                        onChange={this.onChange}
                        className={"auth-input" + (errorUsername ? " error" : "")}
                        name="username"
                        value={this.state.value.username}
                        onKeyDown={this.keyPress}
                        id="auth-username"
                        placeholder="Username"
                        onAnimationEnd={() => this.setState({ errorUsername: false })}
                    />
                    <Input
                        className={"auth-input" + (errorPassword ? " error" : "") + (password ? " encode" : "")}
                        type="text"
                        name="password"
                        onChange={this.onChange}
                        value={this.state.value.password}
                        onKeyDown={this.keyPress}
                        placeholder="Password"
                        id="auth-password"
                        onAnimationEnd={() => this.setState({ errorPassword: false })}
                    />
                </div>
                {
                    errors.valueSeq().map((error, key) => {
                        return <AuthError error={error}
                            key={key} />
                    })
                }
                <div className="info-wrapper" onMouseEnter={this.infoToggle} onMouseLeave={this.infoToggle}>
                    <Info />
                    {showInfo &&
                        <div className="info-inner">
                            <div className="header">
                                <h1>Basic authorization</h1>
                            </div>
                            <div className="body">
                                <div className="body-inner">
                                    {description && description.split("\n").map((line, i) => {
                                        return <h1 className="line" key={i}>{line}</h1>
                                    })}
                                </div>
                                <div className="body-inner">
                                    <h1>Basic Login with UID and password</h1>
                                </div>
                            </div>
                        </div>
                    }
                </div>
            </div>)
    }

    static propTypes = {
        name: PropTypes.string.isRequired,
        errSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,
        onChange: PropTypes.func,
        schema: ImPropTypes.map,
        authorized: ImPropTypes.map
    }
}
