import React, { PureComponent } from "react"
import PropTypes from 'prop-types'
import { stringify } from 'core/utils'
import { ExpandMore, ExpandLess } from "@material-ui/icons"

export default class ResponseExample extends PureComponent {
    static propTypes = {
        response: PropTypes.instanceOf(Iterable),

        getComponent: PropTypes.func.isRequired
    }

    constructor(props) {
        super(props)
        this.state = {
            open: true
        }
    }

    render() {
        let {
            responses,

            getComponent
        } = this.props


        const HighlightCode = getComponent("highlightCode")

        let {
            open
        } = this.state

        return <div className="opblock-section">
            <div className="opblock-section-header close" onClick={() => this.setState((state) => ({ open: !state.open }))}>
                <div className="icon">
                    {open ? <ExpandLess /> : <ExpandMore />}
                </div>
                <h4 className="opblock-title">Response Sample</h4>
            </div>
            {open &&
                responses.map(([code, value]) => {
                    let category
                    if ((code >= 100) && (code < 200)) {
                        category = "informational"
                    } else if ((code >= 200) && (code < 300)) {
                        category = "successful"
                    } else if ((code >= 300) && (code < 400)) {
                        category = "redirects"
                    } else if ((code >= 400) && (code < 500)) {
                        category = "client-error"
                    } else if ((code >= 500) && (code < 600)) {
                        category = "server-error"
                    } else {
                        category = "not-defined"
                    }

                    return <div className="example-wrapper" key={code}>
                        <div className="example-header">
                            <h1>Response to Code:</h1>    
                            <div className={"response-code " + category}>
                                <h1>{ code }</h1>
                            </div>
                        </div>
                        <HighlightCode max showLineNumbers={true} className="example" value={stringify(value)} getComponent={getComponent} />
                    </div>
                })
            }
        </div>
    }
}