import React from "react"
import PropTypes from "prop-types"

export default class NavLink extends React.Component {
    static propTypes = {
        specSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,

        tagObj: PropTypes.object.isRequired,
        tag: PropTypes.string.isRequired,
        position: PropTypes.number.isRequired
    }

    constructor(props) {
        super(props)
        this.state = {
            open: false,
            position: props.position
        }

        this.listenToScroll = this.listenToScroll.bind(this)
        this.scrollTo = this.scrollTo.bind(this)
        this.navScroll = this.navScroll.bind(this)
    }

    componentDidMount() {
        let { processedTag } = this.props
        window.addEventListener('scroll', this.listenToScroll)
        console.log(processedTag)
        this.interval = setInterval(() => {
            const tagObject = document.getElementById("operations-tag-wrapper-" + processedTag)
            tagObject && tagObject.addEventListener('click', () => this.navScroll())
        }, 4000)
    }

    componentWillUnmount() {
        let { processedTag } = this.props
        window.removeEventListener('scroll', this.listenToScroll)
        const tagObject = document.getElementById("operations-tag-wrapper-" + processedTag)
        tagObject.removeEventListener('click', () => this.navScroll())
        clearInterval(this.interval)
    }

    navScroll() {
        let { processedTag } = this.props
        const mainNav = document.getElementById("navigation-wrapper")
        const thisTagNav = document.getElementById("navigation-link-" + processedTag)
        mainNav.scrollTo({
            top: thisTagNav.offsetTop, 
            left: 0, 
            behavior: 'smooth'
        })
    }

    listenToScroll() {
        
        let {
            tagObj,
            tag,
            processedTag,
            position
        } = this.props 

        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop
        
        var minPos = -1
        tagObj.get("operations").map((op, i) => {
            let operationId = op.getIn(["operation", "__originalOperationId"]) || op.getIn(["operation", "operationId"]) || op.get("id")
            
            let isShownKey = ["operations", processedTag, operationId, i]
            var offset = document.getElementById(isShownKey.join("-")) ? document.getElementById(isShownKey.join("-")).offsetTop : 0
            
            if (minPos === -1) {
                minPos = offset
            } else if ((scrollTop - offset > -(window.innerHeight / 2)) && (offset > minPos)) {
                minPos = offset
            }
        })
        this.setState({
            open: position === document.getElementById("operations-tag-wrapper-" + processedTag).offsetTop,
            position: minPos
        })
    }

    scrollTo() {
        const id = "operations-tag-wrapper-" + this.props.processedTag
        window.scrollTo({
            top: (document.getElementById(id) ? document.getElementById(id).offsetTop : (document.body.scrollTop || document.documentElement.scrollTop)),
            left: 0,
            behavior: 'smooth'
        })
    }


    render() {
        let {
            tag,
            tagObj,
            getComponent,
            processedTag
        } = this.props

        const NavSubLink = getComponent("NavSubLink")

        let { open, position } = this.state
        
        return <div className="outer navigation-link tag" id={"navigation-link-" + processedTag}>
            <div className={open ? "navigation-link tag header open" : "navigation-link tag header"} onClick={this.scrollTo}>
                <span className="navigation-linktag">{ tag }</span>
            </div>
            {open &&
                <div className="navigation-link tag body">
                    {tagObj.get("operations").map((op, i) => {
                        const path = op.get("path")
                        const method = op.get("method")
                        return <NavSubLink 
                            key={`${path}-${method}`}
                            op={op}
                            i={i}
                            position={position}
                            tag={tag}
                            processedTag={processedTag}
                        />
                    }).toArray()
                    }
                </div>    
            }
        </div>
    }
}