import React from "react"
import { Iterable } from "immutable"
import PropTypes from "prop-types"

export default class Responses extends React.Component {
  static propTypes = {
    tryItOutResponse: PropTypes.instanceOf(Iterable),
    responses: PropTypes.instanceOf(Iterable).isRequired,
    getComponent: PropTypes.func.isRequired,
  }

  static defaultProps = {
    tryItOutResponse: null,
  }


  render() {
    let {
      responses,
      tryItOutResponse,
      getComponent,
    } = this.props

    const Response = getComponent("response")

    let vars = { "code": true, "condition": false }

    responses.entrySeq().map(([code, response]) => {
      if (response.get("description")) {
        vars.condition = true
      }
    })


    return (
      <div className="responses-wrapper">
        <div className="opblock-section-header">
          <h4>Response Codes</h4>
        </div>
        {vars.condition
          ? <div className="responses-inner">
            <table className="responses-table">
              <thead>
                <tr className="responses-header">
                  <td className="col_header response-col_status">code</td>
                  <td className="col_header response-col_description">condition</td>
                </tr>
              </thead>
              <tbody>
                {
                  responses.entrySeq().map(([code, response]) => {
                    let className = tryItOutResponse && tryItOutResponse.get("status") === code ? "response_current" : ""
                    return (
                      <Response
                        key={code}
                        className={className}
                        code={code}
                        response={response}
                        getComponent={getComponent}
                        showCondition={vars.condition}
                      />
                    )
                  }).toArray()
                }
              </tbody>
            </table>
          </div>
          : <div className="responses-inner flex">
            {
              responses.entrySeq().map(([code, response], i) => {
                // category of the response => to color codes
                let category
                if ((code >= 100) && (code < 200)) {
                  category = "informational"
                } else if ((code >= 200) && (code < 300)) {
                  category = "successful"
                } else if ((code >= 300) && (code < 400)) {
                  category = "redirects"
                } else if ((code >= 400) && (code < 500)) {
                  category = "client-error"
                } else if ((code >= 500) && (code < 600)) {
                  category = "server-error"
                } else {
                  category = "not-defined"
                }
                return <div className={"response-code " + category + " row " + i} key={code + i}>
                  <h1>{code}</h1>
                </div>
              })

            }
          </div>
        }
      </div>
    )
  }
}
