import React from "react"
import PropTypes from "prop-types"
import { Info } from "@material-ui/icons"

export default class ApiKeyAuth extends React.Component {
    static propTypes = {
        authorized: PropTypes.object,
        getComponent: PropTypes.func.isRequired,
        errSelectors: PropTypes.object.isRequired,
        authActions: PropTypes.object.isRequired,
        authSelectors: PropTypes.object.isRequired,
        schema: PropTypes.object.isRequired,
        name: PropTypes.string.isRequired,
        onChange: PropTypes.func
    }

    constructor(props, context) {
        super(props, context)
        let { name, schema } = this.props
        let value = this.getValue()

        this.state = {
            name: name,
            schema: schema,
            value: value,
            showInfo: false,
            error: false
        }

        this.infoToggle = this.infoToggle.bind(this)
        this.confirm = this.confirm.bind(this)
    }

    componentDidMount() {
        const authButton = document.getElementById("auth-button")
        authButton.addEventListener('click', () => this.confirm())
    }

    componentWillUnmount() {
        const authButton = document.getElementById("auth-button")
        authButton.removeEventListener('click', () => this.confirm())
    }

    infoToggle() {
        this.setState((state) => ({
            showInfo: !state.showInfo
        }))
    }

    getValue() {
        let { name, authorized } = this.props

        return authorized && authorized.getIn([name, "value"])
    }

    onChange = (e) => {
        let value = e.target.value
        let newState = Object.assign({}, this.state, { value: value })

        this.setState(newState)
    }

    keyPress = (e) => {
        if (e.keyCode == 13) {
            this.confirm()
            if (this.state.value) {
                document.activeElement.blur()
            }
        }
    }

    confirm() {
        let { onChange, submit, logout, authSelectors, name } = this.props
        
        onChange(this.state)
        
        let authorized = this.state.value ? true : false
        this.setState({ error: !authorized })
        
        if (authorized) {
            submit()
        } else if (authSelectors.authorized().get(name)) {
            logout()
        }
    }

    render() {
        let { schema, getComponent, errSelectors, name } = this.props
        const Input = getComponent("Input")
        const AuthError = getComponent("authError")

        let errors = errSelectors.allErrors().filter(err => err.get("authId") === name)

        name = name || schema.get("name")
        let description = schema.get("description")

        let { error, showInfo, value } = this.state

        return (
            <div className={"auth-simple"} id="auth-simple-api">
                <Input
                    className={"auth-input" + (error ? " error" : "") + (value ? " encode" : "")}
                    type="text"
                    onChange={this.onChange}
                    placeholder={name}
                    value={this.state.value}
                    onKeyDown={this.keyPress}
                    onAnimationEnd={() => this.setState({ error: false })} 
                />
                {
                    errors.valueSeq().map((error, key) => {
                        return <AuthError error={error}
                            key={key} />
                    })
                }
                <div className="info-wrapper" onMouseEnter={this.infoToggle} onMouseLeave={this.infoToggle}>
                    <Info />
                    {// given information only for bearer login
                    showInfo && 
                        <div className="info-inner">
                            <div className="header">
                                <h1>{name || schema.get("name")}</h1>
                                <h1>(Bearer Authorization)</h1>
                            </div>
                            <div className="body">
                                <div className="body-inner">
                                    {description && description.split("\n").map((line, i) => {
                                        return <h1 className="line" key={i}>{line}</h1>
                                    })}
                                </div>
                                <div className="body-inner">
                                    <h1>{"Name: " + schema.get("name")}</h1>
                                    <h1>{"In: " + schema.get("in")}</h1>
                                </div>
                                <div className="body-inner">
                                    <h1>Bearer with scheme and token</h1>
                                    <h1>e.g. "Bearer WCT jsdjaksfhksj72rjhr...=="</h1>
                                </div>
                            </div>
                        </div>
                    }
                </div>
            </div>
        )
    }
}
