import React from "react"
import PropTypes from "prop-types"
import ImPropTypes from "react-immutable-proptypes"
import Im from "immutable"
import { createDeepLinkPath, escapeDeepLinkPath, sanitizeUrl } from "core/utils"

export default class OperationTag extends React.Component {

    static defaultProps = {
        tagObj: Im.fromJS({}),
        tag: "",
    }

    static propTypes = {
        tagObj: ImPropTypes.map.isRequired,
        tag: PropTypes.string.isRequired,

        layoutSelectors: PropTypes.object.isRequired,
        layoutActions: PropTypes.object.isRequired,

        getConfigs: PropTypes.func.isRequired,
        getComponent: PropTypes.func.isRequired,

        children: PropTypes.element,
    }

    render() {
        const {
            tagObj,
            tag,
            children,
            processedTag,

            getConfigs,
            getComponent,
        } = this.props

        let {
            docExpansion,
            deepLinking,
        } = getConfigs()

        const isDeepLinkingEnabled = deepLinking && deepLinking !== "false"

        const Collapse = getComponent("Collapse")
        const Markdown = getComponent("Markdown")
        const DeepLink = getComponent("DeepLink")
        const Link = getComponent("Link")

        let tagDescription = tagObj.getIn(["tagDetails", "description"], null)
        let tagExternalDocsDescription = tagObj.getIn(["tagDetails", "externalDocs", "description"])
        let tagExternalDocsUrl = tagObj.getIn(["tagDetails", "externalDocs", "url"])


        return <div className={"opblock-tag-section is-open"} id={"operations-tag-wrapper-" + processedTag}>
            <h1>{tag}</h1>
            <div className="opblock-description">
                {tagDescription &&
                    <small>
                        <Markdown source={tagDescription} />
                    </small>
                }
                {tagExternalDocsDescription && <div><small>
                    {tagExternalDocsDescription}
                    {tagExternalDocsUrl ? ": " : null}
                    {tagExternalDocsUrl && <Link
                        href={sanitizeUrl(tagExternalDocsUrl)}
                        onClick={(e) => e.stopPropagation()}
                        target="_blank"
                    >
                        {tagExternalDocsUrl}</Link>
                    }
                </small></div>
                }
            </div>

            <Collapse isOpened={true}>
                {children}
            </Collapse>
        </div>
    }
}
